// 面包屑导航
.crumbs {
    ul {
        width: 100%;
        font-size: 16px;
        li {
            height: 60px;
            line-height: 55px;
            span {
                margin: 0 10px;
            }
            float: left;
            .active {
                color: #ed593f;
            }
        }
    }
}

// <!-- 头部通用结构 -->
.header {
    height: 100px;
    // background-color: #ccc;
    .logo {
        float: left;
        width: 211px;
        height: 100px;
        background: url(../images/public/jilogo.png) 0px 10px no-repeat;
    }
    .m-right {
        float: left;
        margin-left: 150px;
        width: 900px;
        li {
            float: left;
            height: 45px;
            font-size: 16px;
            span {
                display: block;
                float: left;
                line-height: 45px;
                padding: 0 5px;
            }
        }
        .app,
        .wx {
            margin-top: 5px;
            padding: 0 5px;
            height: 33px;
            position: relative;
            cursor: pointer;
            span {
                line-height: 33px;
            }
            .QRCode {
                position: absolute;
                width: 180px;
                height: 200px;
                background-color: #fff;
                text-align: center;
                img {
                    padding: 5px;
                }
                p {
                    margin-top: -5px;
                }
            }
            &:hover .QRCode {
                display: block;
            }
        }
        .app {
            background-color: #f2f2f2;
            border-right: 1px #dcdcdc solid;
            .QRCode {
                display: none;
                top: 32px;
                left: -30px;
                z-index: 20;
            }
            .icon1 {
                float: left;
                margin-top: 9px;
                width: 16px;
                height: 16px;
                background: url(../images/public/telephone.png);
            }
            .icon2 {
                float: left;
                margin-top: 12px;
                width: 12px;
                height: 12px;
                background: url(../images/public/direction_down.png);
            }
        }
        .wx {
            background-color: #f2f2f2;
            .QRCode {
                display: none;
                top: 32px;
                left: -24px;
                z-index: 20;
            }
            .icon1 {
                float: left;
                margin-top: 9px;
                width: 19px;
                height: 19px;
                background: url(../images/public/weixin.png);
            }
            .icon2 {
                float: left;
                margin-top: 12px;
                width: 12px;
                height: 12px;
                background: url(../images/public/direction_down.png);
            }
        }
        .pic1,
        .pic2,
        .pic3 {
            margin-right: 30px;
            img {
                float: left;
                display: block;
                margin-top: 5px;
                width: 35px;
                height: 35px;
                line-height: 45px;
            }
            span {
                float: left;
            }
        }
        .pic1 {
            margin-left: 20px;
        }
    }
    .nav {
        float: left;
        // width: 800px;
        margin-left: -38px;
        // margin: 0 auto;
        padding-top: 12px;
        // background-color: #fff;
        li {
            float: left;
            padding: 5px 22px;
            font-size: 16px;
            margin-right: 80px;
            border-radius: 13px;
            height: auto;
        }
        .nav-active {
            background-color: #ed593f;
            color: #fff;
        }
    }
    .register {
        float: right;
        width: 100px;
        height: 100px;
        line-height: 93px;
        text-align: center;
        font-size: 16px;
    }
}

// 底部通用结构
.footer {
    width: 100%;
    background-color: #1b1b1b;
    margin-top: 30px;
    font-size: 16px;
    padding-top: 70px;
    padding-bottom: 100px;
    p {
        width: 1200px;
        padding-top: 20px;
        margin: 0 auto;
        // background-color: #fff;
        padding-bottom: 15px;
        padding-top: 20px;
        color: #fff;
        a {
            display: block;
            float: left;
            margin-right: 80px;
            color: #fff;
        }
        span {
            float: right;
            color: #fff;
        }
    }
    .info {
        border-bottom: 1px dashed #fff;
    }
}